.projects {
  position: relative;
  height: 100vh;

  &__timeline {
    position: absolute;
    content: "";

    &__main {
      &--merge {
        transform: scaleY(0);
        stroke-dasharray: 250px;
        stroke-dashoffset: 250px;
      }

      &--checkout {
        stroke-dasharray: 65px;
        stroke-dashoffset: 65px;
      }
    }

    &__secondary {
      stroke-dasharray: 240px;
      stroke-dashoffset: 240px;
    }

    &__dot {
      &--branch {
        opacity: 0;
      }

      &--checkout {
        opacity: 0;
      }

      &--merge {
        opacity: 0;
      }
    }
  }

  &__content {
    padding-top: 9vh;
  }
}

.wrapper__project {
  width: 31%;
  aspect-ratio: 3/2;
  position: absolute;
  top: 10%;
  left: 50%;

  &:first-child {
    padding-left: min(1.5em);
  }

  &:last-child {
    padding-right: min(1.5em);
  }

  img {
    width: 100%;
    height: 100%;
  }

  &__detail {
    position: relative;
    z-index: -1;
    top: -100%;
    scale: 0;
  }
}

.wrapper__project__1 {
  transform: translate(-50%, -50%) scale(0.5) rotate(-5deg);
}

.wrapper__project__2 {
  transform: translate(-50%, -50%) scale(0.5) rotate(5deg);
}

.wrapper__project__3 {
  transform: translate(-50%, -50%) scale(0.5) rotate(0deg);
}
